import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			id: '',
			is_miniBind: uni.getStorageSync('is_miniBind'),
			list: [],
			isDisable: true
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		cancelPay() {
			uni.switchTab({
				url: '/pages/tabar/shopcart'
			})
		},
		bindWechat() {
			rs.Toast('该账号未绑定微信，请先绑定再来支付');
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/tabar/user'
				})
			}, 1000)

		},

















































































































































































































		// 临时订单信息
		temOrder() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				id: this.id
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
			}, obj)
			rs.getRequests("temOrderMsg", params, (res) => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.list = data.data
				} else {
					uni.showToast({
						title: data.msg,
						duration: 2000,
						icon: "none"
					});
				}

			})
		}
	},
	onShow() {
		this.temOrder();
	},
	onLoad(option) {
		this.id = option.id;
	}
};
